#contacts {
  scroll-margin-top: 100px;
}

.contacts {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 0 30px;
  margin-inline: var(--pad);
  margin-block-start: auto;
  padding-inline: 80px;
  padding-inline: clamp(24px, 0.1824rem + 6.5882vw, 80px);
  padding-block: clamp(24px, 0.6529rem + 4.2353vw, 60px);
  background-color: var(--clr-white);
  border: 1px solid var(--clr-green);
  border-radius: clamp(1.875rem, 0.7691rem + 5.5294vw, 4.8125rem) clamp(1.875rem, 0.7691rem + 5.5294vw, 4.8125rem) 0 0;
  &__item {
    display: flex;
    flex-direction: column;
  }
  .client {
    grid-column: 1 / span 4;
  }
  .team {
    grid-column: 5 / span 4;
  }
  .office {
    grid-column: 9 / span 4;
  }
  &__title {
    margin-block-end: 30px;
    margin-block-end: clamp(16px, 0.6706rem + 1.6471vw, 1.875rem);
  }
  &__person {
    margin-block-start: auto;
    margin-block-end: 24px;
    margin-block-end: clamp(16px, 0.8118rem + 0.9412vw, 1.5rem);
  }
  &__name {
    margin-block-end: 5px;
  }
  &__position {
    color: var(--clr-gray);
    font-size: 14px;
  }
  &__links {
    display: flex;
    flex-direction: column;
    gap: clamp(4px, 0.1088rem + 0.7059vw, 10px) 30px;
    margin-block-start: auto;
    a {
      color: var(--clr-green);
      font-size: 17px;
      font-size: clamp(15px, 0.8904rem + 0.2353vw, 1.0625rem);
      font-weight: var(--fw-s);
      overflow-wrap: anywhere;
      transition: color var(--tr);
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-yellow);
    }
  }
  &__address {
    margin-block-start: auto;
    margin-block-end: 25px;
    font-style: normal;
  }
}

@media (max-width: 1200px) {
  .contacts {
    gap: 40px 0;
    .client,
    .team,
    .office {
      grid-column: 1 / -1;
    }
    &__title br {
      display: none;
    }
    &__links {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}
