.card-service {
  position: relative;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: min-content 1fr;
  gap: 0 5px;
  min-block-size: clamp(220px, 12.2676rem + 7.4118vw, 283px);
  padding: clamp(24px, 1.1235rem + 1.8824vw, 40px);
  background-color: var(--clr-white);
  border: 1px solid var(--clr-green);
  border-radius: clamp(40px, 2.0294rem + 2.3529vw, 60px);
  transition: background-color var(--tr);
  &__title {
    grid-column: 1 / -1;
    grid-row: 1 / span 1;
  }
  &__desc {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    align-self: end;
  }
  &__btn {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
    align-self: end;
  }
  &__img {
    overflow: hidden;
    position: absolute;
    z-index: -1;
    inset-inline-end: 0;
    inset-block-start: 0;
    inline-size: 200px;
    block-size: 160px;
    border-radius: 30px;
    opacity: 0;
    transition: opacity var(--tr), transform var(--tr);
    img {
      block-size: 100%;
    }
  }
}

.card-service:hover,
.card-service.show-img {
  z-index: unset;
  background-color: transparent;
  .card-service__btn {
    background-color: var(--clr-green);
    svg {
      fill: var(--clr-white);
    }
  }
  .card-service__img {
    opacity: 1;
  }
}

@media (max-width: 618px) {
  .card-service:hover,
  .card-service.show-img {
    pointer-events: none;
    .card-service__btn {
      pointer-events: auto;
    }
  }
}
