.card-case {
  --r: clamp(40px, 2.0294rem + 2.3529vw, 60px);
  background-color: var(--clr-beige);
  border-radius: var(--r);
  transition: background-color var(--tr);
  &__top {
    position: relative;
  }
  &__img {
    --h: 350px;
    overflow: hidden;
    aspect-ratio: 1.6285 / 1;
    border-radius: var(--r);
    clip-path: circle(70%);
    transition: clip-path var(--tr);
    img {
      block-size: 100%;
    }
  }
  &__logo-wrap {
    position: absolute;
    inset-inline-start: 50%;
    inset-block-end: clamp(0.75rem, 0.0912rem + 3.2941vw, 2.5rem);
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 188px;
    block-size: 74px;
    background-color: hsl(0 0% 100% / .9);
    border-radius: 5px;
  }
  &__logo {
    inline-size: 128px;
    block-size: auto;
  }
  &__btn {
    position: absolute;
    inset-inline-start: 76%;
    inset-block-start: 25%;
    transform: translate(-50%, -50%) scale(.15);
    opacity: 0;
    transition: opacity var(--tr) 0s, transform var(--tr) 0s;
  }
  &__btm {
    margin-inline: clamp(12px, 0.3265rem + 2.1176vw, 30px);
    margin-block: clamp(20px, 1.0147rem + 1.1765vw, 30px);
    text-align: center;
  }
  &__title {
    margin-block-end: 10px;
  }
}

.card-case:hover {
  background-color: transparent;
  .card-case__img {
    clip-path: circle(37%);
  }
  .card-case__btn {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
    transition: opacity var(--tr) .15s, transform var(--tr) .15s;
  }
}

@media (max-width: 428px) {
  .card-case {
    &__img {
      aspect-ratio: 1.2727 / 1;
    }
  }
}
