.about-hero {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: min-content 1fr;
  gap: 0 30px;
  margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
  &__title {
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    margin-block-end: clamp(12px, -0.3794rem + 5.6471vw, 60px);
    font-size: clamp(34px, 1.4191rem + 3.5294vw, 64px);
    line-height: clamp(40px, 1.5588rem + 4.7059vw, 80px);
  }
  &__desc {
    grid-column: 1 / span 6;
    grid-row: 2 / span 1;
    &>p+p {
      margin-block-start: 30px;
    }
  }
  &__img {
    grid-column: 7 / -1;
    grid-row: 1 / span 2;
    min-inline-size: 472px;
    justify-self: end;
  }
}

@media (max-width: 1024px) {
  .about-hero {
    grid-template-rows: unset;
    gap: 0;
    &__title {
      grid-column: 1 / -1;
    }
    &__desc {
      grid-column: 1 / -1;
      margin-block-end: 40px;
    }
    &__img {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      min-inline-size: unset;
      max-inline-size: 490px;
      justify-self: center;
    }
  }
}
