.event-hero {
  position: absolute;
  inset: 0 0px clamp(0.75rem, -2.2857rem + 15.1786vw, 5rem) 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    margin-block: auto clamp(12px, 0.3265rem + 2.1176vw, 30px);
  }
  &__text {
    margin-block-end: clamp(40px, 1.0882rem + 7.0588vw, 100px);
    font-size: clamp(16px, 0.7647rem + 1.1765vw, 26px);
  }
  &__icons {
    --c-g: clamp(16px, 0.6706rem + 1.6471vw, 30px);
    --p: 12px;
    --w: calc((100% - var(--c-g)) / 2);
    display: -ms-grid;
    display: grid;
    grid-template-columns: minmax(0, var(--w)) minmax(0, var(--w));
    justify-content: center;
    gap: 0 var(--c-g);
    padding-inline: var(--m);
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px 0;
  }
  &__item-img {
    inline-size: auto;
    max-block-size: 134px;
    max-block-size: 100px;
    img {
      width: unset;
      block-size: 100%;
    }
  }
  &__item-text {
    max-inline-size: 370px;
    margin-block-end: 10px;
  }
}

@media (max-width: 768px) {
  .event-hero {
    &__text {
      font-family: var(--ff-prim);
      font-size: clamp(16px, 0.7768rem + 1.1161vw, 21px);
      font-weight: var(--fw-m);
    }
  }
}

@media (max-width: 640px) {
  .event-hero {
    &__item-img {
      block-size: clamp(68px, 0.125rem + 20.625vw, 134px);
    }
  }
}

// EVENT HERO NO ICONS
.event-hero-no-icons {
  position: absolute;
  inset: 0 0 clamp(0.75rem, -2.2857rem + 15.1786vw, 5rem) 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__title {
    margin-block-end: clamp(12px, 0.3265rem + 2.1176vw, 30px);
  }
  &__text {
    font-size: clamp(16px, 0.7647rem + 1.1765vw, 26px);
  }
}

@media (max-width: 768px) {
  .event-hero-no-icons {
    justify-content: end;
    &__text {
      margin-block-end: clamp(10px, -6.0714rem + 33.4821vw, 160px);
      font-family: var(--ff-prim);
      font-weight: var(--fw-m);
    }
  }
}

// EVENT CONTENT =================================================
.event-content {
  display: grid;
  gap: 20px 0;
  &__block {
    display: grid;
    grid-template-columns: 4fr 6fr;
    gap: 0 60px;
    padding: clamp(30px, 0.8494rem + 5.1282vw, 70px);
    background-color: var(--clr-white);
    border: 1px solid var(--clr-green);
    border-radius: clamp(60px, 2.3382rem + 7.0588vw, 120px);
  }
  &__title {}
  &__desc {}
  &__list {
    list-style: none;
    margin: 0;
    padding-inline-start: clamp(0px, -1.0256rem + 5.1282vw, 40px);
  }
  &__list>li+li {
    margin-block-start: 20px;
  }
  &__item {
    position: relative;
    padding-inline-start: 30px;
  }
  &__item-text {
    font-size: 12px;
  }
  &__item::before {
    content: '';
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 6px;
    display: block;
    inline-size: 12px;
    block-size: 12px;
    background-color: var(--clr-green);
    border-radius: 100vw;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
}

@media (max-width: 992px) {
  .event-content {
    &__block {
      grid-template-columns: 1fr;
      gap: 30px 0;
    }
  }
}

@media (max-width: 768px) {}

