.about-slider {
  --tr: .5s ease-in-out;
  overflow: hidden;
  position: relative;
  display: grid;
  gap: 30px 0;
  inline-size: 100%;
  margin-block-end: clamp(40px, 1.0882rem + 7.0588vw, 100px);
  &__track {
    display: flex;
    block-size: 400px;
    block-size: clamp(220px, 9.5147rem + 21.1765vw, 25rem);
  }
  &__slide {
    position: relative;
    flex-basis: 0;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    color: hsl(0 0% 0% / 0.5);
    font-size: 60px;
    font-weight: 700;
    border-radius: 40px;
    transition: flex-grow var(--tr), margin-inline-end var(--tr);
  }
  &__img {
    inline-size: 100%;
    block-size: 400px;
    img {
      block-size: 100%;
    }
    p {
      position: absolute;
      inset-block-end: clamp(20px, 0.7372rem + 2.5641vw, 40px);
      margin-inline: clamp(20px, 0.7372rem + 2.5641vw, 40px);
      color: var(--clr-white);
      font-family: var(--ff-sec);
      font-size: clamp(16px, 0.8462rem + 0.7692vw, 22px);
      font-weight: var(--fw-m);
      line-height: 1.3;
      transition: opacity var(--tr);
    }
  }
  &__slide.wide {
    flex-grow: 1;
  }
  &__slide.narrow {
    flex-grow: 0.4;
    p {
      opacity: 0;
    }
  }
  &__slide.gap {
    margin-inline-end: clamp(12px, -1.3993rem + 4.4776vw, 30px);
  }
  &__controls {
    justify-self: end;
    display: flex;
    gap: 0 10px;
  }
  &__info {
    position: absolute;
    inset-inline-start: 0;
    inset-block-end: 0;
    inline-size: 768px;
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .about-slider {
    &__info {
      position: unset;
      inline-size: unset;
      margin-inline: var(--pad);
    }
  }
}

@media (max-width: 768px) {
  .about-slider {
    inline-size: unset;
    margin-inline: calc(-1 * var(--pad));
    &__track {
      overflow-x: auto;
      gap: 0 8px;
      padding-inline: var(--pad);
    }
    &__track::-webkit-scrollbar {
      display: none;
    }
    &__track {
      scrollbar-width: none;
    }
    &__slide {
      flex: 0 0 280px;
    }
    &__controls {
      display: none;
    }
  }
}
