.filters {
  position: relative;
  display: grid;
  margin-block-start: -20px;
  margin-block-end: clamp(40px, 2.0294rem + 2.3529vw, 60px);
  // ======================
  position: fixed;
  z-index: 2;
  inset-inline-start: 50%;
  transform: translateX(-50%);
  inline-size: min(1100px, 100%);
  // background: #ccc;
  &__tablist {
    position: relative;
    display: flex;
    gap: 0 30px;
    transition: margin-block-end var(--tr), padding-block-end var(--tr);
    .filter-tab:first-child {
      margin-inline-start: auto;
    }
  }
  &__tablist.active {
    margin-block-end: 20px;
    padding-block-end: 20px;
  }
  &__tablist::before {
    content: '';
    position: absolute;
    inset-inline-start: 0;
    inset-block-end: 0px;
    inline-size: 0;
    block-size: 1px;
    background-color: var(--clr-green);
    transition: inline-size var(--tr) 0s;
  }
  &__tablist.active::before {
    inline-size: 100%;
    transition: inline-size var(--tr) .3s;
  }
}

.filter-tab {
  display: flex;
  align-items: center;
  gap: 0 5px;
  inline-size: max-content;
  font-size: 14px;
  font-weight: var(--fw-m);
  line-height: 1.4;
  text-transform: uppercase;
  transition: color var(--tr);
  svg {
    inline-size: 20px;
    block-size: 20px;
    fill: var(--clr-green);
    transition: transform var(--tr);
  }
}

.filter-tab:hover {
  color: var(--clr-green);
}

.filter-tab.active {
  color: var(--clr-green);
  svg {
    transform: rotate(180deg);
  }
}

.filter-panel {
  display: grid;
  grid-template-rows: 0fr;
  opacity: 1;
  transition: opacity var(--tr), grid-template-rows var(--tr);
  &__inner {
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
  }
}

.filter-panel.expanded {
  grid-template-rows: 1fr;
  opacity: 1;
  transition: opacity var(--tr) .6s, grid-template-rows var(--tr);
}

.filter-option {
  inline-size: max-content;
  padding: 10px;
  color: var(--clr-gray);
  background-color: var(--clr-white);
  font-size: 14px;
  border: 1px solid var(--clr-border);
  border-radius: 100vw;
  transition: color var(--tr), background-color var(--tr), border-color var(--tr), ;
}

.filter-option.active,
.filter-option.active-first {
  color: var(--clr-white);
  background-color: var(--clr-green);
  border-color: var(--clr-green);
}

@media (max-width: 768px) {
  .filters {
    margin-block-start: 16px;
    &__tablist {
      .filter-tab:first-child {
        margin-inline-start: unset;
      }
    }
  }
}

@media (max-width: 428px) {
  .filters__tablist {
    gap: 0 12px;
  }
  .filter-tab {
    gap: 0;
    text-transform: none;
  }
  .filter-panel {
    &__inner {
      overflow-x: auto;
      flex-wrap: nowrap;
      margin-inline: calc(-1 * var(--pad));
      padding-inline: var(--pad);
    }
    &__inner::-webkit-scrollbar {
      display: none;
    }
    &__inner {
      scrollbar-width: none;
    }
    &__inner>* {
      flex: 0 0 auto;
    }
  }
}
