.awards {
  margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
  padding-block: 150px;
  padding-block: clamp(80px, 3.3529rem + 8.2353vw, 9.375rem);
  color: var(--clr-white);
  background-color: var(--clr-green);
  &__inner {
    position: relative;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
  }
  &__title {
    grid-column: 1 / span 4;
  }
  &__slider {
    grid-column: 5 / -1;
    position: unset;
    margin-inline: unset;
  }
  .swiper-wrapper {
    display: flex;
    flex-direction: row;
    margin-block-end: 60px;
  }
  &__slider-btns {
    position: absolute;
    z-index: 2;
    inset-inline-start: 0;
    inset-block-start: 150px;
    display: flex;
    gap: 0 clamp(5px, 0.1949rem + 0.5882vw, 10px);
  }
  &__slide {
    display: grid;
    grid-template-columns: min-content 1fr;
    gap: 0 15px;
    padding-inline-end: 30px;
  }
  &__desc {
    grid-column: 2 / span 1;
  }
  &__desc-title {
    margin-block-end: 10px;
  }
  &__desc-text {}
  &__img {
    grid-column: 1 / span 1;
    inline-size: clamp(30px, 1.4044rem + 2.3529vw, 50px);
    block-size: clamp(30px, 1.4044rem + 2.3529vw, 50px);
    fill: var(--clr-yellow);
  }
}

@media (max-width: 1170px) {
  .awards {
    &__inner {
      gap: clamp(20px, 0.3088rem + 4.7059vw, 60px) 0;
    }
    &__title {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__slider {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    &__slider-btns {
      inset-inline-start: unset;
      inset-inline-end: 0;
      inset-block-start: 0;
    }
  }
}

@media (max-width: 640px) {
  .awards {
    &__slider {
      padding-block-end: 60px;
    }
    &__slide {
      padding-inline-end: unset;
    }
    &__slider-btns {
      inset-inline-start: 0;
      inset-inline-end: unset;
      inset-block-start: unset;
      inset-block-end: 0;
    }
  }
}
