.about-content {
  margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
  &__block {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
  }
  &__title {
    grid-column: 1 / span 5;
    margin-block-end: 20px;
    font-size: clamp(26px, 1.2485rem + 1.8824vw, 42px);
  }
  &__desc {
    grid-column: 6 / -1;
    margin-block-end: clamp(40px, 2.0294rem + 2.3529vw, 60px);
    ul {
      display: grid;
      gap: 20px 0;
      margin: 0;
    }
  }
  & :last-child {
    margin-block-end: 0;
  }
  &__desc>p+p {
    margin-block-start: 20px;
  }
  &__stats {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 30px;
    margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
  }
  &__stats-title {
    grid-column: 1 / span 3;
    font-family: var(--ff-sec);
    font-size: 22px;
    font-weight: var(--fw-m);
    line-height: 1.3;
  }
  &__stats-list {
    grid-column: 4 / -1;
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0 30px;
  }
  &__stats-num {
    margin-block-end: clamp(0px, -0.3846rem + 1.9231vw, 15px);
    font-family: var(--ff-sec);
    font-size: clamp(18px, 0.8173rem + 1.5385vw, 30px);
    font-weight: var(--fw-e);
    line-height: 1.3;
  }
  &__stats-text {
    font-size: 14px;
  }
  &__slogan {
    color: var(--clr-green);
    font-family: var(--ff-sec);
    font-size: clamp(36px, 1.6346rem + 3.0769vw, 60px);
    font-weight: var(--fw-e);
    line-height: 1.2;
    text-align: center;
  }
}

@media (max-width: 1100px) {
  .about-content {
    &__block {
      gap: 0;
    }
    &__title {
      grid-column: 1 / -1;
    }
    &__desc {
      grid-column: 1 / -1;
      ul {
        padding-inline-start: 15px;
      }
    }
  }
}

@media (max-width: 1050px) {
  .about-content {
    &__stats {
      gap: 30px 0;
    }
    &__stats-title {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    &__stats-list {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}

@media (max-width: 640px) {
  .about-content {
    &__stats-list {
      grid-template-columns: 1fr;
    }
    &__stats-item {
      margin-block-end: 20px;
    }
  }
}
