.mobile-menu-overlay {
  position: fixed;
  z-index: 1;
  background-color: hsla(0, 0%, 0%, 0);
  transition: background-color var(--tr);
}

.mobile-menu-overlay.active {
  inset: 0;
  background-color: hsla(0, 0%, 0%, 0.5);
  cursor: pointer;
}

.clients-title,
.news-title {
  margin-block-end: clamp(20px, 0.3088rem + 4.7059vw, 60px);
}

.news-single-top {
  position: absolute;
  inset: 0;
  inset-block-start: 170px;
  @media (max-width: 640px) {
    inset-block-start: unset;
    inset-block-end: clamp(24px, -4.5rem + 30vw, 120px);
  }
}

.news-single-date {
  margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  color: var(--clr-green);
}

.news-single-title {
  inset-block-start: 170px;
  font-size: clamp(20px, 0.2147rem + 5.1765vw, 64px);
  line-height: clamp(26px, 0.3544rem + 6.3529vw, 80px);
  @media (max-width: 640px) {
    font-family: var(--ff-m);
  }
}

.title-404 {
  margin-block-end: 30px;
  color: var(--clr-green);
  text-align: center;
}

.text-404 {
  text-align: center;
}
