.clients {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 0 30px;
  margin-block-end: 200px;
  margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
  &__title {
    grid-column: 1 / span 3;
  }
  &__sliders {
    grid-column: 4 / -1;
  }
  &__slider {}
  &__slide {}
  &__slide-img {
    inline-size: 170px;
    block-size: 80px;
  }
  .swiper-wrapper {
    transition-timing-function: linear !important;
  }
}

@media (max-width: 1024px) {
  .clients {
    gap: 30px 0;
    &__title {
      grid-column: 1 / -1;
    }
    &__sliders {
      grid-column: 1 / -1;
    }
  }
}
