  .testims {
    position: relative;
    margin-block-end: clamp(40px, 0.9615rem + 7.6923vw, 100px);
    &__title {
      margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
    }
    &__slider {
      position: initial;
    }
    &__slide {
      display: grid;
      grid-template-columns: 1fr 1.3fr;
      gap: 40px 30px;
      padding: 60px;
      padding: clamp(24px, 0.6529rem + 4.2353vw, 3.75rem);
      background-color: var(--clr-white);
      border: 1px solid var(--clr-green);
      border-radius: 68px;
      border-radius: clamp(40px, 1.8412rem + 3.2941vw, 4.25rem);
    }
    &__slide-logo {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
      align-self: center;
      max-inline-size: 375px;
      max-inline-size: clamp(167px, 4.247rem + 30.9524vw, 23.4375rem);
      img {
        // width: unset;
      }
    }
    &__slide-text {
      grid-column: 2 / span 1;
      grid-row: 1 / span 1;
      margin-block-end: clamp(20px, 0.6548rem + 2.9762vw, 40px);
    }
    &__slide-text>p+p {
      margin-block-start: 20px;
    }
    &__slide-person {
      grid-column: 2 / -1;
      grid-row: 2 / span 1;
      display: grid;
      grid-template-columns: 1fr min-content;
      gap: 10px 20px;
    }
    &__slide-person-img {
      grid-column: 2 / -1;
      grid-row: 1 / span 2;
      inline-size: 80px;
      block-size: 80px;
      border: 1px solid var(--clr-green);
      border-radius: 100vw;
    }
    &__slide-person-name {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
      justify-self: end;
      align-self: end;
      font-size: 17px;
      font-weight: var(--fw-s);
    }
    &__slide-person-position {
      grid-column: 1 / span 1;
      grid-row: 2 / span 1;
      justify-self: end;
      align-self: start;
      max-inline-size: 330px;
      color: var(--clr-gray);
      font-size: 14px;
      text-align: right;
    }
    &__slider-btns {
      position: absolute;
      inset-inline-end: 0;
      inset-block-start: 0;
      display: flex;
      gap: 0 10px;
    }
  }

  @media (max-width: 992px) {
    .testims {
      &__slide {
        gap: 16px 0;
      }
      &__slide-logo {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
        justify-self: center;
        align-self: unset;
        margin-block: 24px;
      }
      &__slide-text {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
      }
      &__slide-person {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
        display: grid;
      }
    }
  }

  @media (max-width: 428px) {
    .testims {
      &__slider {
        margin-inline: calc(-1 * var(--pad));
        .swiper-wrapper {
          overflow-x: auto;
          overflow-y: hidden;
          gap: 0 8px;
          inline-size: unset;
          padding-inline: var(--pad);
        }
        .swiper-wrapper::-webkit-scrollbar {
          display: none;
        }
        .swiper-wrapper {
          scrollbar-width: none;
        }
      }
      &__slide {
        flex: 0 0 280px;
      }
      &__slide-text {
        position: relative;
        overflow: hidden;
        max-block-size: 220px;
        transition: max-block-size var(--tr);
      }
      &__slide-text::before {
        content: '';
        position: absolute;
        inset: 50% 0 0 0;
        background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      }
      &__slide-text.expanded {
        max-block-size: 1000px;
      }
      &__slide-text.expanded::before {
        background: none;
      }
      &__slide-person {
        grid-template-columns: 1fr;
        justify-items: end;
        gap: 8px 0px;
      }
      &__slide-person-img {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
        inline-size: 40px;
        block-size: 40px;
      }
      &__slide-person-name {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        justify-self: unset;
        align-self: unset;
        font-size: 16px;
        font-weight: var(--fw-b);
      }
      &__slide-person-position {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
        justify-self: unset;
        align-self: unset;
        max-inline-size: unset;
        font-size: 10px;
      }
      &__slider-btns {
        display: none;
      }
    }
  }
