.case-hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  padding-block-start: 145px;
  &__logo {
    overflow: hidden;
    display: grid;
    justify-content: center;
    align-items: center;
    inline-size: 200px;
    block-size: 200px;
    padding-inline: 20px;
    background-color: hsl(0 0% 100% / .9);
    border-radius: 5px;
  }
  &__title {
    font-size: clamp(20px, 0.7324rem + 2.5882vw, 42px);
    font-weight: var(--fw-e);
    line-height: 1.2;
  }
  &__categs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  &__categ {
    padding: 10px;
    color: inherit;
    font-size: 14px;
    border: 1px solid var(--clr-white);
    border-radius: 15px;
  }
}

@media (max-width: 768px) {
  .case-hero {
    &__title {
      font-weight: var(--fw-m);
      line-height: 1.3;
    }
  }
}

@media (max-width: 640px) {
  .case-hero {
    padding-block-start: 200px;
    &__logo {
      block-size: unset;
      padding-block: 20px 24px;
    }
    &__categs {
      margin-block-start: auto;
    }
  }
}

@media (max-width: 428px) {
  .case-hero {
    justify-content: end;
    padding-block-start: unset;
    &__logo {
      inline-size: 120px;
      padding: 20px;
    }
    &__categs {
      margin-block-start: 40px;
    }
  }
}
