.simple-slider-wrapper {
  display: grid;
  gap: 20px 0;
  margin-block-end: clamp(40px, 1.0882rem + 7.0588vw, 100px);
}

.simple-slider {
  inline-size: 100%;
  &__slide {}
  &__img {
    overflow: hidden;
    display: block;
    aspect-ratio: 1.4225;
    max-block-size: 400px;
    border: 1px solid var(--clr-border);
    border-radius: 40px;
    img {
      block-size: 100%;
    }
  }
  &__btns {
    margin-inline-start: auto;
    display: flex;
    gap: 15px;
  }
}

@media (max-width: 768px) {
  .simple-slider-wrapper {
    margin-inline: calc(-1 * var(--pad));
  }
  .simple-slider {
    .swiper-wrapper {
      overflow-x: auto;
      overflow-y: hidden;
      gap: 0 8px;
      inline-size: unset;
      padding-inline: var(--pad);
    }
    .swiper-wrapper::-webkit-scrollbar {
      display: none;
    }
    .swiper-wrapper {
      scrollbar-width: none;
    }
    &__slide {
      flex: 0 0 280px;
    }
    &__btns {
      display: none;
    }
  }
}
