.news {
  position: relative;
  margin-block-end: clamp(80px, 3.3529rem + 8.2353vw, 150px);
  &__title {
    margin-block-end: 40px;
    margin-block-end: clamp(30px, 1.6397rem + 1.1765vw, 2.5rem);
  }
  &__btn {
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: 0;
  }
  &__cards-block.auto-grid {
    --min-w: 280px;
  }
}

@media (max-width: 640px) {
  .news {
    display: grid;
    &__cards-block.auto-grid {
      overflow-x: auto;
      overflow-y: hidden;
      display: flex;
      gap: 0 8px;
      margin-block-end: 20px;
      margin-inline: calc(-1 * var(--pad));
      padding-inline: var(--pad);
      &>* {
        flex: 0 0 280px;
      }
    }
    &__cards-block::-webkit-scrollbar {
      display: none;
    }
    &__cards-block {
      scrollbar-width: none;
    }
    &__btn {
      position: relative;
      justify-self: center;
    }
  }
}

.news-page__cards-block.auto-grid {
  --min-w: 280px;
  --r-g: clamp(32px, 0.8706rem + 5.6471vw, 80px);
}

.news-single-content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  margin-block-end: 20px;
  ul {
    margin-block: 30px;
  }
  img {
    border-radius: 30px;
  }
}

.news-single-content>p+p {
  margin-block-start: 20px;
}

.news-single-content>* {
  grid-column: 3 / 11;
}

.news-single-content h3 {
  margin-block: 40px 20px;
}

@media (max-width: 640px) {
  .news-single-content>* {
    grid-column: 1 / -1;
  }
}
