.btn {
  overflow: hidden;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0 5px;
  block-size: 60px;
  padding: 0px 40px;
  color: inherit;
  background-color: var(--clr-white);
  font-size: 14px;
  font-weight: var(--fw-m);
  line-height: 1;
  text-transform: uppercase;
  border: 1px solid var(--clr-green);
  border-radius: 100vw;
  &__text {
    position: relative;
    z-index: 1;
    inline-size: max-content;
    transition: color var(--tr);
  }
  svg {
    position: relative;
    z-index: 1;
    inline-size: 20px;
    block-size: 20px;
    fill: var(--clr-green);
    transition: fill var(--tr);
  }
}

.btn::before {
  content: '';
  position: absolute;
  z-index: 0;
  inset: 0;
  background-color: var(--clr-green);
  border-radius: 100vw;
  transform: translateX(-100%);
  transition: transform var(--tr);
}

.btn:hover {
  .btn__text {
    color: var(--clr-white);
  }
  svg {
    fill: var(--clr-white);
  }
}

.btn:hover::before {
  transform: translateX(0%);
}

//--- btn dark
.btn--dark {
  color: var(--clr-white);
  background-color: var(--clr-green);
  border: 1px solid var(--clr-white);
  svg {
    fill: var(--clr-white);
  }
}

.btn--dark::before {
  background-color: var(--clr-white);
}

.btn--dark:hover {
  .btn__text {
    color: var(--clr-text);
  }
  svg {
    fill: var(--clr-green);
  }
}

//--- btn circle
.btn-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  inline-size: 58px;
  block-size: 58px;
  border: 1px solid var(--clr-green);
  border-radius: 100vw;
  transition: background-color var(--tr);
  svg {
    inline-size: 20px;
    block-size: 20px;
    fill: var(--clr-green);
    transition: fill var(--tr);
  }
}

.btn-circle:hover {
  background-color: var(--clr-green);
  svg {
    fill: var(--clr-white);
  }
}

.btn-circle--dark {
  background-color: var(--clr-green);
  svg {
    fill: var(--clr-white);
  }
}
