.hero {
  margin-block-end: clamp(80px, 2.1765rem + 14.1176vw, 200px);
  &__title {
    display: grid;
    gap: 10px 0;
    margin-block-end: 200px;
    margin-block-end: clamp(60px, 0.4559rem + 16.4706vw, 12.5rem);
    padding-block-start: 40px;
  }
  &__title>* {
    display: block;
  }
  &__still {
    font-size: clamp(24px, 0.6529rem + 4.2353vw, 60px);
    line-height: clamp(29px, 0.6125rem + 6vw, 80px);
  }
  &__anim-wrapper {
    --duration: 1000ms;
    display: grid;
    grid-template-columns: 0fr;
    justify-self: start;
    align-content: center;
    padding: 15px 40px;
    color: var(--clr-white);
    background-color: var(--clr-green);
    border-radius: 100vw;
    transition-property: grid-template-columns;
    transition-duration: var(--duration);
    transition-timing-function: ease-in-out;
  }
  &__anim-wrapper.expanded {
    grid-template-columns: 1fr;
    .hero__anim-text {
      opacity: 1;
    }
  }
  &__anim-text {
    overflow: hidden;
    font-size: clamp(24px, 0.9353rem + 2.8235vw, 48px);
    font-size: clamp(17px, 0.3331rem + 3.6471vw, 48px);
    font-weight: var(--fw-m);
    line-height: 1.2;
    overflow-wrap: normal;
    opacity: 0;
    transition-property: opacity;
    transition-duration: var(--duration);
    transition-timing-function: ease-in-out;
  }
}

@media (max-width: 1024px) {
  .hero {
    &__anim-wrapper {
      block-size: clamp(75px, 2.9228rem + 8.8235vw, 9.375rem);
      padding-inline: clamp(1.25rem, 0.529rem + 2.6954vw, 2.5rem);
      padding-block: unset;
    }
  }
}

@media (max-width: 992px) {
  .hero {
    &__anim-text {
      display: block;
    }
  }
}
