.cases-other {
  position: relative;
  margin-block-end: clamp(80px, 3.3529rem + 8.2353vw, 150px);
  &__title {
    margin-block-end: 40px;
  }
}

.cases-slider {
  position: unset;
  inline-size: 100%;
  &__btns {
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: 0;
    display: flex;
    gap: 10px;
  }
  .card-case {
    padding-bottom: 10px;
  }
}

@media (max-width: 510px) {
  .cases-slider {
    inline-size: unset;
    margin-inline: calc(-1 * var(--pad));
    //
    .swiper-wrapper {
      overflow-x: auto;
      overflow-y: hidden;
      gap: 0 8px;
      inline-size: unset;
      padding-inline: var(--pad);
    }
    .swiper-wrapper::-webkit-scrollbar {
      display: none;
    }
    .swiper-wrapper {
      scrollbar-width: none;
    }
    .swiper-wrapper>* {
      flex: 0 0 280px;
    }
    .case-slide {
      margin-inline-end: 0 !important;
    }
    &__btns {
      display: none;
    }
  }
}
