.btn {
  overflow: hidden;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0 5px;
  block-size: 60px;
  padding: 0px 40px;
  color: inherit;
  background-color: var(--clr-white);
  font-size: 14px;
  font-weight: var(--fw-m);
  line-height: 1;
  text-transform: uppercase;
  border: 1px solid var(--clr-green);
  border-radius: 100vw;
}

.btn__text {
  position: relative;
  z-index: 1;
  inline-size: max-content;
  transition: color var(--tr);
}

.btn svg {
  position: relative;
  z-index: 1;
  inline-size: 20px;
  block-size: 20px;
  fill: var(--clr-green);
  transition: fill var(--tr);
}

.btn::before {
  content: "";
  position: absolute;
  z-index: 0;
  inset: 0;
  background-color: var(--clr-green);
  border-radius: 100vw;
  transform: translateX(-100%);
  transition: transform var(--tr);
}

.btn:hover .btn__text {
  color: var(--clr-white);
}

.btn:hover svg {
  fill: var(--clr-white);
}

.btn:hover::before {
  transform: translateX(0%);
}

.btn--dark {
  color: var(--clr-white);
  background-color: var(--clr-green);
  border: 1px solid var(--clr-white);
}

.btn--dark svg {
  fill: var(--clr-white);
}

.btn--dark::before {
  background-color: var(--clr-white);
}

.btn--dark:hover .btn__text {
  color: var(--clr-text);
}

.btn--dark:hover svg {
  fill: var(--clr-green);
}

.btn-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  inline-size: 58px;
  block-size: 58px;
  border: 1px solid var(--clr-green);
  border-radius: 100vw;
  transition: background-color var(--tr);
}

.btn-circle svg {
  inline-size: 20px;
  block-size: 20px;
  fill: var(--clr-green);
  transition: fill var(--tr);
}

.btn-circle:hover {
  background-color: var(--clr-green);
}

.btn-circle:hover svg {
  fill: var(--clr-white);
}

.btn-circle--dark {
  background-color: var(--clr-green);
}

.btn-circle--dark svg {
  fill: var(--clr-white);
}

.btn-slider {
  display: flex;
  justify-content: center;
  align-items: center;
  inline-size: 55px;
  block-size: 55px;
  border: 1px solid;
  border-radius: 20px;
  transition: background-color var(--tr);
}

.btn-slider svg {
  inline-size: 12px;
  block-size: 16px;
  pointer-events: none;
  transition: fill var(--tr);
}

.btn-slider--prev svg {
  transform: translateX(-2px) rotate(0deg);
}

.btn-slider--next svg {
  transform: translateX(2px) rotate(180deg);
}

.btn-slider--dark {
  border-color: var(--clr-green);
}

.btn-slider--dark svg {
  fill: var(--clr-green);
}

.btn-slider--dark:hover {
  background-color: var(--clr-green);
}

.btn-slider--dark:hover svg {
  fill: var(--clr-white);
}

.btn-slider--light {
  border-color: var(--clr-white);
}

.btn-slider--light svg {
  fill: var(--clr-white);
}

.btn-slider--light:hover {
  background-color: var(--clr-white);
}

.btn-slider--light:hover svg {
  fill: var(--clr-green);
}

.btn-slider.swiper-button-disabled,
.btn-slider:disabled {
  background-color: var(--clr-light);
  border-color: var(--clr-light);
}

.btn-slider.swiper-button-disabled svg,
.btn-slider:disabled svg {
