.case-slider {
  --tr: .5s ease-in-out;
  overflow: hidden;
  display: grid;
  gap: 30px 0;
  inline-size: 100%;
  margin-block-end: clamp(40px, 1.0882rem + 7.0588vw, 100px);
  &__track {
    display: flex;
    block-size: 400px;
    block-size: clamp(220px, 9.5147rem + 21.1765vw, 25rem);
  }
  &__slide {
    flex-basis: 0;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    color: hsl(0 0% 0% / 0.5);
    font-size: 60px;
    font-weight: 700;
    border-radius: 40px;
    cursor: pointer;
    transition: flex-grow var(--tr), margin-inline-end var(--tr);
  }
  &__img {
    inline-size: 100%;
    block-size: 400px;
    img {
      block-size: 100%;
    }
  }
  // &__slide.hidden {
  //   inline-size: 0;
  // }
  &__slide.wide {
    flex-grow: 1;
  }
  &__slide.narrow {
    flex-grow: 0.2;
  }
  &__slide.gap {
    margin-inline-end: clamp(12px, -1.3993rem + 4.4776vw, 30px);
  }
  &__controls {
    justify-self: end;
    display: flex;
    gap: 0 10px;
  }
}

@media (max-width: 768px) {
  .case-slider {
    inline-size: unset;
    margin-inline: calc(-1 * var(--pad));
    &__track {
      overflow-x: auto;
      gap: 0 8px;
      padding-inline: var(--pad);
    }
    &__track::-webkit-scrollbar {
      display: none;
    }
    &__track {
      scrollbar-width: none;
    }
    &__slide {
      flex: 0 0 280px;
    }
    &__controls {
      display: none;
    }
  }
}
