.about {
  overflow: hidden;
  margin-block-end: 200px;
  margin-block-end: clamp(80px, 2.1765rem + 14.1176vw, 12.5rem);
  padding-block: 150px;
  padding-block: clamp(60px, 1.6324rem + 10.5882vw, 9.375rem);
  color: var(--clr-white);
  background-color: var(--clr-green);
  &__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: clamp(3.125rem, 1.9485rem + 5.8824vw, 6.25rem) 30px;
  }
  &__imgs {
    position: relative;
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    max-inline-size: clamp(209px, 6.2154rem + 34.2353vw, 500px);
    block-size: max-content;
  }
  #about__w-1 {
    position: absolute;
    inset-inline-start: 0px;
    inset-block-start: 0px;
    inline-size: 52%;
    animation: w-expand-fade 2s linear infinite;
  }
  #about__w-2 {
    position: absolute;
    inset-inline-end: 0px;
    inset-block-start: 55%;
    inline-size: 52%;
    animation: w-expand-fade 2s 1s linear infinite;
  }
  &__desc {
    grid-column: 7 / span 6;
    grid-row: 1 / span 1;
  }
  &__desc-title {
    margin-block-end: clamp(20px, 1.0147rem + 1.1765vw, 30px);
  }
  &__desc-text {
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  }
  &__desc-text>p+p {
    margin-block-start: 20px;
  }
  &__stats {
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
    list-style: none;
    display: flex;
    gap: 0 30px;
    margin: 0;
    padding: 0;
  }
  &__stats-item {
    flex-grow: 1;
    flex-basis: 0;
    text-align: center;
  }
  &__stats-title {
    font-family: Unbounded;
    font-size: 80px;
    font-size: clamp(44px, 1.9029rem + 4.2353vw, 5rem);
    font-weight: var(--fw-e);
    line-height: 1.3;
  }
  &__stats-text {
    font-family: Unbounded;
    font-size: clamp(18px, 1.0309rem + 0.4706vw, 22px);
    line-height: 1.3;
  }
}

.about>* {
  grid-column: content;
}

@media (max-width: 1024px) {
  .about {
    &__inner {
      gap: clamp(40px, 1.5588rem + 4.7059vw, 80px) 0;
    }
    &__imgs {
      grid-column: 1 / -1;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    &__stats {
      grid-row: 3 / span 1;
      flex-wrap: wrap;
      gap: 20px 30px;
    }
  }
}

@media (max-width: 390px) {
  .about {
    &__desc {
      display: grid;
    }
    &__desc-btn {
      justify-content: center;
    }
  }
}

@keyframes w-expand-fade {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.23);
  }
}
