h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5 {
  font-family: var(--ff-sec);
  font-weight: var(--fw-m);
  line-height: 1.3;
  overflow-wrap: break-word;
}

h1,
.h1 {
  font-size: clamp(36px, 1.5912rem + 3.2941vw, 64px);
  font-weight: var(--fw-b);
  line-height: clamp(43px, 1.8169rem + 4.3529vw, 80px);
}

h2,
.h2 {
  font-size: 42px;
  font-size: clamp(32px, 1.7647rem + 1.1765vw, 2.625rem);
  font-weight: var(--fw-e);
  line-height: 1.2;
}

h3,
.h3 {
  font-size: 26px;
  font-size: clamp(20px, 1.1088rem + 0.7059vw, 1.625rem);
}

h4,
.h4 {
  font-size: 22px;
  font-size: clamp(18px, 1.0309rem + 0.4706vw, 1.375rem);
}

p,
a {
  overflow-wrap: break-word;
}
