:root {
  --max-container: 1100px;
  --pad: 20px;
  --ff-prim: 'Montserrat', sans-serif;
  --ff-sec: 'Unbounded', sans-serif;
  --fz-base: clamp(16px, 0.9765rem + 0.1176vw, 17px);
  --fz-s: 14px;
  --fw-l: 300;
  --fw-r: 400;
  --fw-m: 500;
  --fw-s: 600;
  --fw-b: 700;
  --fw-e: 800;
  --fw-k: 900;
  --lh: 1.3;
  --clr-green: #50D75D;
  --clr-dark-green: #0d7218;
  --clr-yellow: #EFEF14;
  --clr-gray: #7F7F7F;
  --clr-light: #F2F8F2;
  --clr-border: #CDDDCC;
  --clr-text: #1A1A1A;
  --clr-white: #fff;
  --clr-bg: #FBFBFB;
  --clr-beige: #ECF6EC;
  --tr: .3s ease-in-out;
}
