.card-case-page {
  overflow: hidden;
  position: relative;
  background-color: var(--clr-beige);
  border-radius: clamp(24px, 0.6529rem + 4.2353vw, 60px);
  &__img {
    overflow: hidden;
    position: relative;
    z-index: 0;
    aspect-ratio: 1.425 / 1;
    border-radius: clamp(24px, 0.6529rem + 4.2353vw, 60px);
    &>img {
      block-size: 100%;
      clip-path: circle(100%);
      transition: clip-path var(--tr);
    }
  }
  &__img::before {
    content: '';
    position: absolute;
    z-index: 1;
    inset: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0.90) 100%);
    transition: opacity var(--tr);
  }
  &__logo {
    position: absolute;
    z-index: 2;
    inset-inline-start: 40px;
    inset-block-start: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 188px;
    block-size: 74px;
    background-color: hsl(0 0% 100% / .9);
    border-radius: 5px;
    img {
      inline-size: 128px;
      block-size: auto;
    }
  }
  &__btn {
    position: absolute;
    z-index: 2;
    inset-inline-start: 79%;
    inset-block-start: 25%;
    transform: translate(-50%, -50%) scale(.15);
    opacity: 0;
    transition: opacity var(--tr) 0s, transform var(--tr) 0s;
  }
  &__desc {
    position: absolute;
    z-index: 1;
    inset-inline-start: 40px;
    inset-block-end: 40px;
    color: var(--clr-white);
    transition: opacity var(--tr);
  }
  &__title {
    margin-block-end: 20px;
    font-family: var(--ff-sec);
    font-size: clamp(20px, 1.2029rem + 0.2353vw, 22px);
    font-weight: var(--fw-m);
  }
  &__options {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    span {
      padding: 10px;
      background: rgba(255, 255, 255, 0.10);
      font-size: 14px;
      border: 1px solid var(--clr-white);
      border-radius: 15px;
    }
  }
}

.card-case-page:hover {
  background-color: transparent;
  .card-case-page__img {
    &>img {
      clip-path: circle(40%);
    }
  }
  .card-case-page__img::before {
    opacity: 0;
  }
  .card-case-page__btn {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
    transition: opacity var(--tr) .3s, transform var(--tr) .3s;
  }
  .card-case-page__desc {
    opacity: 0;
  }
}

.card-case-page.hidden {
  display: none;
}

@media (max-width: 1024px) {
  .card-case-page {
    &__logo {
      inset-inline-start: 50%;
      inset-block-start: unset;
      inset-block-end: 12px;
      transform: translateX(-50%);
    }
    &__desc {
      position: unset;
      z-index: unset;
      inset-inline-start: unset;
      inset-block-end: unset;
      margin: 16px 12px 24px;
      color: var(--clr-text);
    }
    &__title {
      text-align: center;
    }
    &__options {
      justify-content: center;
      gap: 4px;
      span {
        padding: 10px;
        color: var(--clr-green);
        background: unset;
        border-color: var(--clr-green);
        text-align: center;
      }
    }
  }
}
