.header {
  position: fixed;
  inset-block-start: 0;
  z-index: 2;
  inline-size: 100%;
  block-size: 70px;
  padding-block-start: 10px;
  &__inner {
    display: flex;
    align-items: center;
    gap: 0 10px;
    inline-size: min(var(--max-container), 100% - (var(--pad) * 2));
    margin-inline: auto;
  }
  &__logo {
    flex-grow: 0;
    flex-shrink: 0;
    display: block;
    inline-size: 60px;
    block-size: 60px;
  }
  &__lang {
    visibility: visible;
    display: flex;
    align-items: center;
    gap: 0 15px;
    margin-inline: 30px;
    margin-inline: clamp(0rem, -11.0294rem + 17.6471vw, 1.875rem);
    margin-inline-start: auto;
    opacity: 1;
    transition: transform var(--tr), opacity var(--tr), visibility var(--tr) 0s;
    button {
      font-size: 14px;
      font-weight: var(--fw-m);
      line-height: 20px;
    }
    .active {
      color: var(--clr-green);
    }
  }
  &__lang.hidden {
    visibility: hidden;
    opacity: 0;
    transform: translateY(-10px);
    transition: transform var(--tr), opacity var(--tr), visibility var(--tr) .3s;
  }
  &__btn {
    flex-grow: 0;
    flex-shrink: 0;
    inline-size: 260px;
    margin-inline-start: auto;
    padding-inline: 0;
  }
}

.header__nav {
  flex-grow: 1;
  display: flex;
  align-items: center;
  block-size: 60px;
}

.header__burger-menu-block {
  overflow: hidden;
  display: flex;
  align-items: center;
  max-inline-size: 60px;
  max-block-size: 60px;
  padding-inline-end: 30px;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-green);
  border-radius: 100vw;
  transition: max-inline-size var(--tr);
}

.header__burger-menu-block.toggle {
  opacity: 1;
  max-inline-size: 100%;
}

.prim-menu {
  display: flex;
  gap: 0 clamp(20px, -2.4265rem + 5.8824vw, 30px);
  margin-inline-start: 40px;
  margin-inline-start: clamp(0rem, -14.7059rem + 23.5294vw, 2.5rem);
  &__item {
    flex-shrink: 0;
    color: inherit;
    font-size: 14px;
    font-weight: 500;
    text-decoration: none;
    transition: color var(--tr);
  }
  &__item:hover {
    color: var(--clr-yellow);
  }
}

@media (max-width: 1000px) {
  .header {
    &__btn {
      justify-content: center;
      inline-size: 60px;
      padding-inline: 0;
      transition: background var(--tr);
      .btn__text {
        display: none;
      }
    }
    &__btn::before {
      content: none;
    }
    &__btn:hover {
      background-color: var(--clr-green);
      border: 1px solid var(--clr-green);
    }
  }
}

@media (max-width: 768px) {
  .header {
    &__inner {
      position: relative;
    }
    &__lang {
      order: 1;
    }
  }
  .header__nav {
    overflow: hidden;
    position: absolute;
    inset-inline-start: 70px;
    inset-block-start: 0px;
    flex-grow: unset;
    flex-direction: column;
    gap: 30px 0;
    align-items: unset;
    inline-size: 60px;
    block-size: 60px;
    padding: 20px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-green);
    border-radius: 30px;
    cursor: pointer;
    transition: inline-size var(--tr), block-size var(--tr);
  }
  .header__nav.toggle {
    inline-size: max-content;
    block-size: max-content;
  }
  .header__burger-menu-block {
    order: 1;
    overflow: unset;
    display: flex;
    flex-direction: column;
    align-items: unset;
    max-inline-size: unset;
    max-block-size: unset;
    margin-inline-start: 30px;
    padding-inline-end: unset;
    background-color: unset;
    border: unset;
    border-radius: unset;
    transition: unset;
    .burger {
      position: absolute;
      inset-inline-start: 0;
      inset-block-start: 0;
    }
  }
  .prim-menu {
    flex-direction: column;
    align-items: end;
    gap: 20px 0;
    margin-inline-start: unset;
    &__item {
      flex-shrink: 0;
      color: inherit;
      font-size: 14px;
      font-weight: 500;
      text-decoration: none;
      transition: color var(--tr-q);
    }
    &__item:hover {
      color: var(--clr-yellow);
    }
  }
}
