.section-top {
  overflow: hidden;
  position: relative;
  block-size: clamp(517px, 25.889rem + 32.1176vw, 790px);
  margin-block-end: clamp(60px, 2.8088rem + 4.7059vw, 100px);
  color: var(--clr-white);
  background-repeat: none;
  background-position: center center;
  background-size: cover;
  text-align: center;
  border-radius: 0 0 clamp(60px, 2.8088rem + 4.7059vw, 100px) clamp(60px, 2.8088rem + 4.7059vw, 100px);
}

.section-top>* {
  position: relative;
  z-index: 1;
}

.section-top::before {
  content: '';
  position: absolute;
  z-index: 0;
  inset: 0;
  background: #1A1A1A;
  opacity: .8;
}

@media (max-width: 768px) {
  .section-top {
    padding-block-end: clamp(24px, 0.7857rem + 3.5714vw, 40px);
  }
}

.section-top--small {
  block-size: clamp(334px, 10.1456rem + 53.6471vw, 790px);
}
