.case-video {
  margin-block-end: clamp(80px, 2.1765rem + 14.1176vw, 200px);
}

.block-youtube-iframe-overlay {
  overflow: hidden;
  position: relative;
  aspect-ratio: 16 / 9;
  inline-size: 100%;
  border-radius: 60px;
  border-radius: clamp(20px, 0.3088rem + 4.7059vw, 3.75rem);
  .youtube-iframe {
    inline-size: 100%;
    block-size: 100%;
  }
  .overlay {
    position: absolute;
    inset: 0;
    cursor: pointer;
  }
  .overlay:hover {
    #circle-expand-fade {
      animation: circle-expand-fade 1s linear infinite;
    }
  }
  .overlay>* {
    position: absolute;
  }
  .overlay__img {
    block-size: 100%;
  }
  .overlay__btn {
    inset-inline-start: 50%;
    inset-block-start: 50%;
    transform: translate(-50%, -50%);
    inline-size: 200px;
    inline-size: clamp(100px, 3.8971rem + 11.7647vw, 12.5rem);
    block-size: 200px;
    block-size: clamp(6.25rem, 3.8971rem + 11.7647vw, 12.5rem);
    &-body {
      inline-size: 150px;
      block-size: 150px;
    }
  }
}

#circle-expand-fade {
  opacity: 0.5;
  transform-origin: center center;
}

@keyframes circle-expand-fade {
  0% {
    opacity: 1;
    transform: scale(0.9);
  }
  100% {
    opacity: 0;
    transform: scale(1.17);
  }
}
