.cases {
  position: relative;
  margin-block-end: 200px;
  margin-block-end: clamp(80px, 2.1765rem + 14.1176vw, 12.5rem);
  &__title {
    margin-block-end: clamp(20px, 0.7794rem + 2.3529vw, 40px);
  }
  &__btn {
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: 0;
  }
  &__block.auto-grid {
    --min-w: 380px;
  }
}

@media (max-width: 428px) {
  .cases {
    display: grid;
    &__block.auto-grid {
      overflow-x: auto;
      overflow-y: hidden;
      display: flex;
      gap: 0 8px;
      margin-inline: calc(-1 * var(--pad));
      padding-inline: var(--pad);
      margin-block-end: 20px;
    }
    &__block::-webkit-scrollbar {
      display: none;
    }
    &__block {
      scrollbar-width: none;
    }
    &__block>* {
      flex: 0 0 280px;
    }
    &__btn {
      justify-self: center;
      position: relative;
      inset-inline-end: 0;
      inset-block-start: 0;
    }
  }
}

@media (max-width: 390px) {
  .cases {
    &__btn {
      justify-content: center;
      justify-self: unset;
    }
  }
}
