.burger {
  flex-shrink: 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  inline-size: 58px;
  block-size: 58px;
  background-color: var(--clr-white);
  border-radius: 100vw;
  cursor: pointer;
  &__inner {
    position: relative;
    inline-size: 20px;
    block-size: 20px;
    &>div {
      position: absolute;
      inline-size: 20px;
      block-size: 2px;
      background-color: var(--clr-green);
    }
    &>div:nth-child(1) {
      transform-origin: top left;
      transition: transform var(--tr);
    }
    &>div:nth-child(2) {
      inset-block-start: 9px;
      transition: opacity var(--tr);
    }
    &>div:nth-child(3) {
      transform-origin: bottom left;
      inset-block-end: 0;
      transition: transform var(--tr);
    }
  }
}

.burger.toggle {
  .burger__inner {
    div:nth-child(1) {
      transform: rotate(43deg) scale(1.35);
    }
    div:nth-child(2) {
      opacity: 0;
    }
    div:nth-child(3) {
      transform: rotate(-43deg) scale(1.35);
    }
  }
}
